.viewmore_link_wrap {
  position: relative;
}

.viewmore_link_wrap .viewmore_link {
  position: relative;
  display: inline-block;
  font-weight: bold;
  font-style: italic;
  font-size: 16px;
  line-height: 26px;
  color: #FF931E;
  text-decoration: none;
}

.viewmore_link_wrap .viewmore_link::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: #FF931E;
}

.viewmore_link_wrap .viewmore_link::after {
  content: ">";
  display: inline-block;
  line-height: 1;
  margin-left: 6px;
  vertical-align: text-bottom;
}

.viewmore_link_wrap .viewmore_link:hover::before {
  width: 100%;
  animation: link-lineloop 0.6s ease-out;
  -webkit-animation: link-lineloop 0.6s ease-out;
}

.view-more {
  position: relative;
}

.link-btn {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.2rem 3.6rem;
  max-width: 100%;
  width: fit-content;
  min-width: 24.0rem;
  height: 6.0rem;
  background: #275BC8;
  border: 1px solid #275BC8;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0.2em;
  color: #FFFFFF;
  text-align: center;
  text-decoration: none;
  outline: none;
  transition: background 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  cursor: pointer;
  z-index: 1;
}

@media only screen and (max-width: 768px) {
  .link-btn {
    max-width: 32.0rem;
    width: 100%;
    padding: 1.2rem 3.6rem;
    justify-content: space-evenly;
    margin-left: auto;
    margin-right: auto;
  }
}

.link-btn span {
  position: relative;
  display: inline-block;
  color: inherit;
  text-decoration: none;
  transition: color 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  z-index: 3;
}

.link-btn .circle-right {
  position: relative;
  display: inline-block;
  width: 3.0rem;
  height: 3.0rem;
  border: 1px solid #FFFFFF;
  border-radius: 50%;
  margin-left: 5.0rem;
  transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  z-index: 3;
}

.link-btn .circle-right::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 42%;
  display: inline-block;
  width: 36%;
  height: 36%;
  border-left: 0;
  border-bottom: 0;
  border-top: 1px solid;
  border-right: 1px solid;
  border-color: inherit;
  transform: translate(-50%, -50%) rotate(45deg);
  z-index: 1;
}

.link-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  background: #FFFFFF;
  width: 100%;
  height: 100%;
  transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  transform-origin: right top;
  transform: scale(0, 1);
  z-index: 1;
}

.link-btn:hover {
  color: #275BC8;
}

.link-btn:hover .circle-right {
  border-color: #275BC8;
}

.link-btn:hover::before {
  transform-origin: left top;
  transform: scale(1, 1);
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  display: none;
  outline: 0;
  opacity: 0;
  transition: opacity .1s linear;
  padding: 1.5rem;
  overflow-x: hidden;
  overflow-y: auto;
  background: rgba(0, 0, 0, 0.5);
}

.modal .modal-dialog {
  position: relative;
  max-width: 70.0rem;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  transition: transform .3s ease-out, -webkit-transform .3s ease-out;
  transform: translate(0, -15%);
  margin: auto;
}

.modal .modal-content {
  position: relative;
  display: block;
  width: 100%;
  background: #FFFFFF;
  border-radius: 0.6rem;
  border: 1px solid rgba(0, 0, 0, 0.2);
  pointer-events: auto;
}

.modal .close {
  position: absolute;
  top: -1.0rem;
  right: -1.0rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.2rem 0.6rem;
  width: 3.0rem;
  height: 3.0rem;
  background-color: #FCD004;
  border-radius: 50%;
  font-size: 1.6rem;
  line-height: 1;
  z-index: 3;
}

.modal .modal-body {
  position: relative;
  padding: 2.0rem;
}

.modal.show {
  opacity: 1;
}

.modal.show .modal-dialog {
  transform: translate(0, 0);
}

.modal-open {
  overflow: hidden;
}

